.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url("../../images/stars.svg"), #512D6D;
  /* background: url("../../images/stars.svg"), #1b1b25; */
  background-repeat: repeat;
  margin-bottom: 0rem;
}

.Badges__hero > .Badges__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.Badges__container {
  width: 100%;
  max-width: 1000px;
  margin: 1.5em auto;
  padding: 0 1rem;
}

.Badges_conf-logo {
  margin-bottom: 2rem;
  height: 80px;
}

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}
.Badgeds__title {
  color: aliceblue;
}

.Badges_list {
  margin-top: 4rem;
}
.ButtonContact__animation {
  animation: pulsate 3s infinite;
  /* transform-origin: center; */
}
@keyframes pulsate {
   40%
  {
    transform: scale( 1.04 );
  } 
}
