.Badge {
  /* background: #ffffff; */
  background: transparent;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: 380px;
  margin-bottom: 10px;
  padding: 1rem;
  width: 100%;
}
.Badge__h1 {
  font-size: 80px;
  color: aliceblue;
  text-transform: uppercase;
}

.Badge__h2 {
  color: aliceblue;
  font-size: 60px;
  width: 17ch;
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
}

@media screen and (max-width: 600px) {
  .Badge__h1 {
    font-size: 70px;
  }
  
  .Badge__h2 {
    font-size: 2.5em;
  }
}

.Badge__border {
  border: 3px solid #23989e !important;
}

.Badge__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1b1b25;
  display: flex;
  justify-content: center;
}

.Badge__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.Badge__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  /* background: #f4f4f7; */
  /* border-radius: 8px 8px 8px 8px; */
}

.Badge__avatar {
  /* border-radius: 50%; */
  margin-right: 1rem;
  width: 120px;
  height: 120px;
  border: 3px solid aliceblue;
  border-radius: 28px;
}

.Badge__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #98ca3f;
  font-weight: bold;
  font-style: italic;
}

.Badge__footer-icon-social{
  margin-bottom: 2em;
  margin-top: 2em;
}

.Badge__domain {
  text-decoration: underline;
  font-weight: bold;
}

.Badge__icon-social{
  border: 3px solid #f4f4f7;
  margin-left: 1.5rem !important;
  margin-right: 1.5rem !important;
  border-radius: 28px;
}

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}
