.Footer {
  background: #512d6d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: 70px;
  margin-bottom: 0px;
  margin-top: 2rem;
}

.Footer__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  color: aliceblue !important;
}

.page-footer {
  background: #512d6d;
  color: aliceblue !important;
}

.Footer__color {
  color: aliceblue !important;
}

.tooltip-custom {
  position: relative;
}

/* Tooltip box */
.tooltip-custom:before {
  content: attr(data-tooltip);
  position: absolute;
  width: 100px;
  background-color: #062b45;
  color: #fff;
  text-align: center;
  padding: 10px;
  line-height: 1.2;
  border-radius: 6px;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.6s;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  font-size: 0.75em;
  visibility: hidden;
}

/* Tooltip arrow */
.tooltip-custom:after {
  content: "";
  position: absolute;
  bottom: 75%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  opacity: 0;
  transition: opacity 0.6s;
  border-color: #062b45 transparent transparent transparent;
  visibility: hidden;
}

.tooltip-custom:hover:before,
.tooltip-custom:hover:after {
  opacity: 1;
  visibility: visible;
}
